export default {
	data() {
		return {
			articleList: [],
			articleType: ""
		}
	},
	onLoad:function(e) {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		this.articleType = e.type;
		if(this.articleType == "" ){
			console.log("文章类型为空,获取列表失败！");
			uni.showToast({
			    icon: "none", 
				title: '文章类型为空,获取列表失败！',
			    duration: 2000
			});
			uni.hideToast();
			uni.hideLoading();
			return;
		}
		uni.request({
			//TODO 替换成查询文章列表的url
			url: 'https://unidemo.dcloud.net.cn/api/news?articleType='+this.articleType,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.articleList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取历史文章列表失败！");
				uni.showToast({
				    icon: "none", 
					title: '获取住历史文章列表失败！',
				    duration: 2000
				});
				uni.hideToast();
				uni.hideLoading();
			},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e){
			var article_id = e.currentTarget.dataset.article_id;
			uni.navigateTo({
				url: '/pages/article-info/article-info?article_id='+article_id,
			});
		}
	}
}
